import loginService from '../services/login';
import programService from '../services/programs';
import reportService from '../services/reports';
import searchService from '../services/search';
import trackService from '../services/tracks';
import userService from '../services/users';
import {
  INIT_USER,
  LOGIN,
  LOGOUT,
  LOGIN_ERROR,
  SET_LOADING
} from '../actions/types';

export const initializeUser = () => async dispatch => {
  try {
    dispatch({
      type: SET_LOADING
    });
    const loggedUserJSON = window.localStorage.getItem('loggedUser');
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      // set token for logged in user
      userService.setToken(user.token);
      reportService.setToken(user.token);
      programService.setToken(user.token);
      searchService.setToken(user.token);
      trackService.setToken(user.token);
      dispatch({
        type: INIT_USER,
        data: user
      });
    }
  } catch (err) {
    dispatch({
      type: LOGIN_ERROR,
      payload: err.response.statusText
    });
  }
};

export const newLogin = (user, errorNotification) => async dispatch => {
  try {
    dispatch({
      type: SET_LOADING
    });
    const newUser = await loginService.login(user);
    window.localStorage.setItem('loggedUser', JSON.stringify(newUser));
    // set token for logged in user
    // blogService.setToken(newUser.token);
    dispatch({
      type: LOGIN,
      data: newUser
    });
  } catch (exception) {
    errorNotification(`vr kyttjtunnus tai salasana`, 4);
  }
};

export const logout = () => async dispatch => {
  window.localStorage.removeItem('loggedUser');
  dispatch({
    type: LOGOUT
  });
};
